@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
    @apply px-2;
    font-size: 20px;
}
li:hover{
    color: royalblue;
}
li:active{
    background-color: royalblue;
    color: white;
    border-radius: 5px;
}
.bg-primary {
    background: white;
}

.bg-secondary {
    background: whitesmoke;
}

.mobile-nav {
    @apply fixed top-10 left-0 bg-white w-full h-1/4 border-b-2;
}

.mobile-nav li {
    @apply w-full h-full text-center border-b-2 ;
}

.project-desc {
    @apply absolute left-0 right-0 bottom-0 top-0 bg-secondary opacity-0 duration-500 hover:opacity-100;
}

.btn {
    @apply bg-primary px-5 py-2 font-bold hover:border-2 border-white rounded;
}

html {
    scroll-behavior: smooth;
}
.d{
    color: royalblue;
}
.social:hover{
    color: royalblue;
}
.lines{
    border-color: royalblue;
}
.frame{
    float: right;
    margin-left: 850px;
    margin-top: -250px;
}
.imgg{
    float: left;
    display: inline-block;
}